import { Constants } from "@/constants";

export class SettingsService {
  public actionAtEnd: string;
  public timeRemaning: number;
  public soundOn = false;
  public changeColorOn = true;
  public countsBeforeAds: number;
  public timesPerMinute = 160;
  public accentBetweenTicks = 4;

  private static _instance: SettingsService;
  public static getInstance(): SettingsService {
    if (localStorage?.getItem("settings") === "1") {
      SettingsService._instance = new SettingsService();
      SettingsService._instance.actionAtEnd = localStorage.getItem("settings.actionAtEnd")!;
      SettingsService._instance.timeRemaning = parseInt(localStorage.getItem("settings.timeRemaning")!, 10);
      SettingsService._instance.soundOn = localStorage.getItem("settings.soundOn")! === "true";
      SettingsService._instance.changeColorOn = localStorage.getItem("settings.changeColorOn")! === "true";
      SettingsService._instance.countsBeforeAds = parseInt(localStorage.getItem("settings.countsBeforeAds")!, 10);
      SettingsService._instance.timesPerMinute = parseInt(localStorage.getItem("settings.timesPerMinute")!, 10);
      SettingsService._instance.accentBetweenTicks = parseInt(localStorage.getItem("settings.accentBetweenTicks")!, 10);
    }
    if (!SettingsService._instance) {
      SettingsService._instance = new SettingsService();
      SettingsService._instance.saveInLocalStorage();
    }
    return SettingsService._instance;
  }

  private constructor() {
    console.log("settings - constructor");
    this.actionAtEnd = Constants.ActionsAtEnd.DEFAULT;
    this.timeRemaning = Constants.TIMEOUT_SECONDS;
    this.countsBeforeAds = Constants.COUNTS_BEFORE_ADS;
    this.timesPerMinute = Constants.TIMES_PER_MINUTE_DEFAULT;
    this.accentBetweenTicks = Constants.ACCENT_BETWEEN_TICKS_DEFAULT;
  }

  private saveInLocalStorage() {
    localStorage?.setItem("settings", "1");
    localStorage?.setItem("settings.actionAtEnd", JSON.stringify(SettingsService._instance.actionAtEnd));
    localStorage?.setItem("settings.timeRemaning", JSON.stringify(SettingsService._instance.timeRemaning));
    localStorage?.setItem("settings.soundOn", JSON.stringify(SettingsService._instance.soundOn));
    localStorage?.setItem("settings.changeColorOn", JSON.stringify(SettingsService._instance.changeColorOn));
    localStorage?.setItem("settings.countsBeforeAds", JSON.stringify(SettingsService._instance.countsBeforeAds));
    localStorage?.setItem("settings.timesPerMinute", JSON.stringify(SettingsService._instance.timesPerMinute));
    localStorage?.setItem("settings.accentBetweenTicks", JSON.stringify(SettingsService._instance.accentBetweenTicks));
  }

  setTimeout(seconds: number): void {
    this.timeRemaning = seconds;
    console.log("settings - this.timeout", this.timeRemaning);
    this.saveInLocalStorage();
  }

  setActionAtEnd(action: string): void {
    this.actionAtEnd = action;
    console.log("settings - this.actionAtEnd", this.actionAtEnd);
    this.saveInLocalStorage();
  }

  setSoundOn(soundOn: boolean): void {
    this.soundOn = soundOn;
    console.log("settings - this.soundOn", this.soundOn);
    this.saveInLocalStorage();
  }

  setChangeColorOn(changeColorOn: boolean): void {
    this.changeColorOn = changeColorOn;
    console.log("settings - this.changeColorOn", this.changeColorOn);
    this.saveInLocalStorage();
  }

  setTimesPerMinute(timesPerMinute: number): void {
    this.timesPerMinute = timesPerMinute;
    console.log("settings - this.timesPerMinute", this.timesPerMinute);
    this.saveInLocalStorage();
  }

  setCountsBeforeAds(countsBeforeAds: number): void {
    this.countsBeforeAds = countsBeforeAds;
    console.log("settings - this.countsBeforeAds", this.countsBeforeAds);
    this.saveInLocalStorage();
  }

  setAccentBetweenTicks(accentBetweenTicks: number): void {
    this.accentBetweenTicks = accentBetweenTicks;
    console.log("settings - this.accentBetweenTicks", this.accentBetweenTicks);
    this.saveInLocalStorage();
  }
}

export const settings = SettingsService;
